.chart svg {
    position: absolute;
    top: 0;
    left: 0;
}
  
.outer {
    fill: transparent;
    stroke: #333;
    stroke-width: 11;
    stroke-dasharray: 534;
    transition: stroke-dashoffset 1s;
}

.off {
    transition: stroke-dashoffset 1.5s ease-in-out;
    stroke-dashoffset: 537;
}

.off.on90 {
    transition: stroke-dashoffset 1.5s ease-in-out;
    stroke-dashoffset: 393;
}

.off.on80 {
    transition: stroke-dashoffset 1.5s ease-in-out;
    stroke-dashoffset: 409;
}

.off.on75 {
    transition: stroke-dashoffset 1.5s ease-in-out;
